<script>
import TableComponent from "@/components/Table/Table.vue";
import PaginationComponent from "@/components/Pagination1/Pagination1.vue";

const toLower = (text) => {
  return text.toString().toLowerCase();
};
// 搜尋條件
const searchByName = (items, term) => {
  if (term) {
    return items.filter(
      (item) =>
        toLower(item.index).includes(toLower(term)) ||
        toLower(item.col1).includes(toLower(term)) ||
        toLower(item.col2).includes(toLower(term)) ||
        toLower(item.col3).includes(toLower(term)) ||
        toLower(item.col4).includes(toLower(term)) ||
        toLower(item.status).includes(toLower(term))
    );
  }
  return items;
};

export default {
  name: "dailyJobApplication-component",
  components: {
    TableComponent,
    PaginationComponent,
  },
  data() {
    return {
      search: "",
      tableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "col1",
          label: "工程編號 (工程名稱)",
          width: "300",
        },
        {
          id: "col2",
          label: "工程日期",
          width: "150",
        },
        {
          id: "col3",
          label: "詳述區域(廠區代號+區域+FAB廠別)",
          width: "300",
        },
        {
          id: "col4",
          label: "工程建立人員(群創)姓名/分機/部門/電子郵件",
          width: "300",
        },
        {
          id: "status",
          label: "工程狀態",
          width: "120",
        },
      ],
      data: [],
      tableData: [],
      tableList: [],
    };
  },
  computed: {},
  methods: {
    // API取得資料
    async getVdrListData() {
      try {
        const res = await this.$store.dispatch("getVdrList");
        this.data = JSON.parse(JSON.stringify(res.data.data));
        this.data.forEach((e, index) => {
          e.index = index + 1;
          e.col1 = `${e.project_No}(${e.project_Name})`;
          e.col2 = `${e.begin_Date} ~ ${e.end_Date}`;
          e.col3 = `${e.ld2}(${e.ld2e}) / ${e.ld3}(${e.ld4}) / ${e.floor}(${e.fab_Id})`;
          e.col4 = `${e.emp_Name}/${e.emp_Extension_No?.trim() || "無"}/${
            e.emp_Dept_Full_Name
          }/${e.emp_Mail}`;
          e.status = this.convertStatus(e.project_Status);
        });
        this.tableData = JSON.parse(JSON.stringify(this.data));
        this.tableList = this.tableData.slice(0, 10);
      } catch (err) {
        console.log(err);
      }
    },
    changePage(page) {
      this.$store.state.currenPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
    searchHandler(evt) {
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        this.$store.state.currentPage = 1;
        const val = evt.target.value;
        this.tableData = searchByName(this.data, val);
        this.tableList = this.tableData.slice(0, 10);
      }
    },
    openApplyDailyJobPermit(item) {
      this.$router.push({
        name: "applyDailyJobPermit",
        params: { obj: JSON.stringify(item) },
      });
    },
  },
  async mounted() {
    this.$store.state.currentPage = 1;
    this.getVdrListData();
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./dailyJobApplication.html"></template>
<style lang="sass" scoped src="../../../../public/styles/custom.sass"></style>
